body {
    overflow-y: scroll;
    /* Show vertical scrollbar */
}

.nav-craft-recipe .nav-link {
    border: 0;
    color: #bbb;
    margin-right: 0px;
    margin-bottom: 0px;
    font-size: larger;

}

.nav-craft-recipe .nav-link:hover {
    color: #fff;
    border-top:0;
    border-left:0;
    border-right:0;
    border-bottom:0;
    margin-right: 0px;
}

.nav-craft-recipe .nav-link.active {
    background-color: #202020;
    border-top:0;
    border-left:0;
    border-right:0;
    border-bottom: 4px solid #ffd100;
    color: #ffd100;
    border-radius: 0%;
    font-size: larger;
}

.nav-craft-recipe .nav-link.active:hover {
    border-top:0;
    border-left:0;
    border-right:0;
    margin-right: 0px;
}

.nav-craft-recipe .nav-link.nav-blood.active {
    border-bottom: 4px solid #c47676;
    color: #c47676;
}

.nav-craft-recipe .nav-link.nav-caster.active {
    border-bottom: 4px solid #B79FCB;
    color: #B79FCB;
}

.nav-craft-recipe .nav-link.nav-hit-power.active {
    border-bottom: 4px solid #487cb4;
    color: #487cb4;
}

.nav-craft-recipe .nav-link.nav-safety.active {
    border-bottom: 4px solid #84D09B;
    color: #84D09B;
}

.text-blood {
    color: #c47676
}

.text-caster {
    color: #B79FCB
}

.text-hit-power {
    color: #487cb4
}

.text-safety {
    color: #84D09B
}

.recipe {
    padding-bottom: 20px;
}

.recipe-group {
    border: 1px solid rgba(255, 255, 255, 0.6);;
}

.recipe-group-title {
    font-weight: 500;
}

.recipe-title {
    margin-bottom: 30px;
}

.recipe-table {
    margin-bottom: 30px;
}

.recipe-info p {
    margin-bottom: 5px;
}

.recipe-rule {
    background-color: rgba(255, 255, 255, 0.5);
    margin-top: 60px;
    width: 60%;
}