.base-equipment-filter select,
.base-equipment-filter select:focus,
.base-equipment-filter input {
    color: #ffffff;
    background-color: #494038;
    border-color: #5c5047;
}

.base-equipment-filter input[type=text]:focus {
    color: #ffffff;
    background-color: #494038;
    border-color: #857467;
}

.base-equipment-filter input[type=text]::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.base-equipment-image {
    width: 100px; 
    object-fit: scale-down; 
    object-position: center;
}