.text-magic {
    color: #84b7ff;
}

.text-unique {
    color: #ecd2a8;
}

.text-set {
    color: #90ff00;
}

.text-craft {
    color: #ffa800;
}

.text-rune {
    color: #ffc107;
}

.text-rare {
    color: #ffff64;
}

.text-socket {
    color: rgba(255,255,255,0.5);
}

.text-content {
    color: #bbb;
}

.modifier-param-range {
    color: #00FFCC;
    background-color: #141B18;
    background-repeat: no-repeat;
    padding-left: 5px;
    padding-right: 5px;
    border: 1px solid #667D6F;
    position: relative;
}

.bg-wrapper {
    background-color: #101010;
}

.bg-content {
    background-color: #202020;
}

.table-d2 {
    background-color: #202020;
    color: #ddd;
}

.table-d2 thead {
    background-color: #29191B;
    color: white;
}

.table-d2 thead th {
    vertical-align: top;
}

.table-d2 tbody tr:hover {
    background-color: #383838;
    color: #ffffff;
}

.navbar-light .navbar-nav .nav-link {
    color: #bbbbbb;
}

.navbar-light .navbar-nav .active>.nav-link,
.navbar-light .navbar-nav .nav-link.active,
.navbar-light .navbar-nav .nav-link.show,
.navbar-light .navbar-nav .show>.nav-link {
    color: #FFFFFF;
}

.navbar-light .navbar-nav .nav-link:hover {
    color: #FFFFFF;
}

.navbar-expand-md .navbar-nav .dropdown-menu {
    background-color: #202020;
}

.dropdown-item {
    color: #bbbbbb;
}

.dropdown-item:hover {
    color: #FFFFFF;
    background-color: #3f3f3f;
}

.dropdown-item.active,
.dropdown-item:active {
    color: #FFFFFF;
    background-color: #646464;
}