.list-item { 
    cursor: pointer;
}

.list-item:hover {
    background-color: #5e5e5e;
}

.list-item-icon > img {
    max-width: 70px;
    max-height: 70px;
}

.list-item-icon {
    background-color: #424242;
    padding: 10px;
    width: 80px;
    height: 80px;
}

.list-item-name {
    color: #90ff00;
    font-weight: 600;
    font-size: 16px;
    white-space: normal;
    line-height: 1.5;
    height: 50px;
}

.list-item-info { 
    font-size: 12px;
    color: rgba(255, 255, 255, 0.8);
}

.set-item-props {
    color: #d0c27d;
}

.scale-part-img {
    width: 76px; height: 152px; object-fit: scale-down; object-position: center;
}

.scale-part-img-container {
    width: 76px;
    height: 152px;
    object-fit: scale-down;
    object-position: 100% 0;
}

.center-block {
    display: block;
    margin-left: auto;
    margin-right: auto;
 }