.text-runeword-key {
    margin-bottom: 0;
    font-size: 10px;
    float: right;
}

.card-runeword-filter:not(.card-outline)>.card-header,
.card-runeword-filter:not(.card-outline)>.card-header a {
    color: #ffffff;
}

.card-runeword-filter:not(.card-outline)>.card-header {
    background-color: #29191B;
}

.card-runeword-filter label {
    color: #ffffff;
}

.card-runeword-filter select,
.card-runeword-filter input {
    color: #ffffff;
    background-color: #494038;
    border-color: #5c5047;
}

.card-runeword-filter input[type=text]:focus {
    color: #ffffff;
    background-color: #494038;
    border-color: #857467;
}

.card-runeword-filter input[type=text]::placeholder {
    color: rgba(255, 255, 255, 0.5);
}

.card-runeword-filter .custom-select {
    background: #494038 url("data:image/svg+xml,%3csvg xmlns='http://www.w3.org/2000/svg' width='4' height='5' viewBox='0 0 4 5'%3e%3cpath fill='%23ffffff' d='M2 0L0 2h4zm0 5L0 3h4z'/%3e%3c/svg%3e") right .75rem center/8px 10px no-repeat;
}

.btn.btn-secondary {
    color: #fff;
    background-color: #494038;
    border-color: #5c5047;
}

.btn.btn-secondary.focus,
.btn.btn-secondary:focus {
    color: #fff;
    background-color: #494038;
    border-color: #5c5047;
    outline: none;
    box-shadow: none;
}

.btn.btn-secondary:hover {
    color: #fff;
    background-color: #5c5047;
    border-color: #494038;
    outline: none;
    box-shadow: none;
}

.btn.btn-secondary.active,
.btn.btn-secondary:active {
    color: #fff;
    background-color: #6e6056 !important;
    outline: #494038 solid 2px !important;
}

.btn.btn-secondary.active.focus,
.btn.btn-secondary.active:focus,
.btn.btn-secondary.active:hover,
.btn.btn-secondary:active.focus,
.btn.btn-secondary:active:focus,
.btn.btn-secondary:active:hover {
    color: #fff;
    background-color: #9b8677 !important;
    border-color: #494038 !important;
    outline: none;
    box-shadow: none;
}