body {
    overflow-y: scroll;
    /* Show vertical scrollbar */
}

.nav-build>.nav-link {
    color: #ffd100;
    margin-right: 0px;
    margin-bottom: 0px;
}

.nav-tabs.flex-column .nav-link {
    margin-right: 0px;
}

.nav-build>.nav-link.active {
    background-color: #202020;
    color: #FFFFFF;
    margin-right: -1px;
}

.skill-panel {
    border: 1px solid white;
    background-color: #181818;
    color: #FFFFFF;
}

.skill-panel-title {
    color: white;
}

.skill-panel-content {
    border-top: 1px solid white;
}

.skill-table-image {
    width: 70px; 
    height: 70px;
}

.skill-table-image img {
    height: 100%;
    width: 100%;
}

.equipment-info-panel {
    padding: 15px;
    margin: 5px;
}